body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  color: #1a2a3a;
  line-height: 24px;
  background-color: #eed6ad;
}

#root {
  display: flex;
  flex-direction: column;
}

#header {
  width: 100%;
  height: 40px;
  z-index: 1100;
  box-sizing: border-box;
  padding: 0 16px;
  background-color: #eed6ad;
  display: flex;
  /* justify-content: space-between; */
  position: fixed;
  top: 0;
}

.header-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 60px;
  width: calc((100% - 4 * 8px) / 5);
  height: 100%;
  box-shadow: 0 0 6px -3px #000;
  cursor: pointer;
  position: relative;
  margin-left: 8px;
}

.header-icon:first-child {
  margin-left: 0;
}

span.icon-link {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

main {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 45px;
  padding: 0 16px;
  box-sizing: border-box;
}

.flex {
  display: flex;
  flex-wrap: wrap;
  flex: initial;
}

.fill-remains {
  flex: 1;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.center-v {
  display: flex;
  align-items: center;
}

.center-h {
  display: flex;
  justify-content: center;
}

.fluid {
  width: 100%;
}

.gutter {
  margin: 8px 0 8px 0;
}

.gutter-top {
  margin-top: 8px;
}

.gutter-bottom {
  margin-bottom: 12px;
}

.highlight {
  box-sizing: border-box;
  border-bottom: medium solid #eec2a2;
}

.ellipsis {
  min-width: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

table,
tbody {
  table-layout: fixed;
  border-collapse: collapse;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

tr {
  width: 100%;
  height: 50px;
}

th,
td {
  vertical-align: middle;
  text-align: center;
}

#stats-detail tr:nth-child(1),
#stats-detail tr:last-child {
  border-bottom: medium solid #eec2a2;
}
#stats-detail tr:last-child {
  border-top: medium solid #eec2a2;
}

#stats-detail th,
#stats-detail td {
  width: 25%;
}

#damage-detail th,
#damage-detail td {
  width: 33.33%;
}

img.sm {
  width: 32px;
  height: 32px;
}

img.md {
  width: 64px;
  height: 64px;
}

.list-item-image > img.md:hover,
.scale {
  transform: scale(1.1);
}

img.lg {
  width: 80px;
  height: 80px;
}

button.active {
  background-color: #abcdef !important;
}

.stats-field {
  min-height: 120px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.19);
  /* , 0 2px 5px rgba(0, 0, 0, 0.23); */
  padding: 8px 8px 8px 0;
}

.stats-field:hover,
.stats-field:focus-within {
  transform: scale(1.01);
}

.avatar {
  min-width: 80px;
  width: 30%;
  height: 100%;
  flex-direction: column;
}

.drawer li,
.avatar > img,
.list-item-image > img,
.list-item-icon > img {
  cursor: pointer;
}

.caption {
  width: 100%;
  text-align: center;
}

.halidom-list {
  overflow: auto;
}

.list-header {
  height: 50px;
  width: 100%;
}

[class^='list-item-'],
[class^='facility-'] {
  display: flex;
  align-items: center;
  justify-content: center;
}

.list-item-image {
  min-width: 64px;
  width: 25%;
}

.list-item-name {
  flex: 1 0 25%;
  justify-content: flex-start;
}

.list-item-icon {
  width: 15%;
}

.list-item-res {
  width: 15%;
  justify-content: flex-start;
}

.btn-groups > button {
  background-color: #f5e8cb;
}

.facility-image {
  width: 25%;
}

.facility-name {
  width: 50%;
  justify-content: flex-start;
}

.facility-slider {
  width: 50%;
  padding: 2%;
  box-sizing: border-box;
}

.facility-icon {
  width: 25%;
}

.facility-icon > img {
  cursor: pointer;
}

.drawer {
  width: 200px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.drawer li {
  height: 45px;
  display: flex;
  align-items: center;
  padding-left: 50px;
  border-bottom: medium groove #eec2a2;
}

.drawer li:active {
  box-shadow: 0 0 10px 5px #eec2a2;
}

.drawer li:hover {
  background-color: #abcdef;
}

.col-2 {
  width: calc((100% - 8px) / 2);
}

.col-2:nth-child(2n) {
  margin-left: 8px;
}

.col-2:nth-child(n + 3) {
  margin-top: 8px;
}

.col-3 {
  width: calc((100% - 8px * 2) / 3);
}

.col-3:not(:nth-child(3n + 1)) {
  margin-left: 8px;
}

@media (min-width: 520px) and (max-width: 959px), (min-width: 1280px) {
  .col-4 {
    width: calc((100% - 8px * 3) / 4);
  }
  .col-4:nth-child(n + 2) {
    margin-left: 8px;
  }
  .col-4:nth-child(5n) {
    margin-left: 0px;
  }
  .col-4:nth-child(-n + 4) {
    margin-top: 0;
  }
  .col-4:nth-child(n + 5) {
    margin-top: 8px;
  }
  #stats-list .list-item:hover {
    background-color: #abcdef;
  }
  .facility-image {
    min-width: 80px;
    width: 20%;
  }
  .facility-name {
    width: 24%;
  }
  .facility-slider {
    width: 32%;
  }
  .facility-icon {
    width: 8%;
  }
}

@media (max-width: 959px) {
  .column {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .column:nth-child(2) {
    margin: 8px 0;
  }
  .column:last-child {
    height: 100vh;
  }
}

@media (min-width: 960px) {
  .column {
    width: calc((100% - 8px * 2) / 3);
    margin-right: 8px;
    height: calc(100vh - 45px);
    display: flex;
    flex-direction: column;
  }
  .column:nth-child(3n) {
    margin-right: 0;
  }
}

div.dialog-flex {
  flex: auto;
}

/* =============== Toggle Button =============== */
.toggle-btn {
  position: relative;
  height: 40px;
  box-sizing: border-box;
  border: 2px solid #999999;
  background: #eeeeee;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
}

.toggle-btn-checkbox {
  display: none;
}

.toggle-btn-label {
  display: block;
  cursor: pointer;
  height: 100%;
  overflow: hidden;
}

.toggle-btn-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  font-size: 0.875rem;
  line-height: 1.75;
  font-weight: 500;
  text-transform: uppercase;
  padding: 0 5px;
  width: 200%;
  height: 100%;
  color: #4a5a6a;
  margin-left: -100%;
  transition: 0.3s;
}

.toggle-btn-switch {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 30px;
  border: 2px solid #999999;
  border-radius: 5px;
  background: #ffffff;
  transition: 0.3s;
}

.toggle-btn.active {
  background: #34a7c1;
}

.toggle-btn-inner.active {
  color: #ffffff;
  margin-left: 0;
}

.toggle-btn-switch.active {
  margin-left: calc(100% - 33px);
}

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield; /* Firefox */
}

/* =============== Slider.js =============== */
.slider {
  position: relative;
  /* min-width: 250px; */
  /* max-width: 500px; */
  width: calc(100% - 80px);
  height: 50px;
  touch-action: none;
  box-sizing: border-box;
  border: 1px solid grey;
}

.slider-rail {
  position: absolute;
  width: 100%;
  background-color: #e9e9e9;
  top: 19px;
  height: 10px;
}

.slider-track {
  position: absolute;
  top: 19px;
  height: 10px;
  background-color: #abe2fb;
}

.slider-handle {
  position: absolute;
  top: 1px;
  height: 46px;
  line-height: 46px;
  cursor: pointer;
  background-color: #1a2a3a;
  color: white;
  text-align: center;
}

/* =============== <input type='range'/> =============== */
input[type='range'] {
  -webkit-appearance: none;
  width: 100%;
  margin: 13.8px 0;
}

input[type='range']:focus {
  outline: none;
}

input[type='range']::-webkit-slider-runnable-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: #bbddcc;
  border-radius: 1.3px;
  border: 0.2px solid #010101;
}

input[type='range']::-webkit-slider-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #34495e;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -14px;
}

input[type='range']:focus::-webkit-slider-runnable-track {
  background: #cce6d9;
}

input[type='range']::-moz-range-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: #bbddcc;
  border-radius: 1.3px;
  border: 0.2px solid #010101;
}

input[type='range']::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #34495e;
  cursor: pointer;
}

input[type='range']::-ms-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

input[type='range']::-ms-fill-lower {
  background: #aad4bf;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}

input[type='range']::-ms-fill-upper {
  background: #bbddcc;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}

input[type='range']::-ms-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #34495e;
  cursor: pointer;
  height: 8.4px;
}

input[type='range']:focus::-ms-fill-lower {
  background: #bbddcc;
}

input[type='range']:focus::-ms-fill-upper {
  background: #cce6d9;
}
